					<el-select v-model="form.sysId" placeholder="请选择系统" @change="chose">
						<el-option v-for="item in system" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item class='left' label="菜单名称" :label-width="formLabelWidth">
					<el-input v-model="form.name" placeholder="请输入系统名称" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="菜单样式" :label-width="formLabelWidth">
					<el-input v-model="form.ico" placeholder="请输入菜单样式" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item class='left' label="菜单标识" :label-width="formLabelWidth">
