<script>
	import Button from '../../button/Button.vue';
	import Table from '../../table/Table.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';

	export default {
		components: {
			Button,
			Table,
			SolidButton
		},
		data() {
			return {
				currentPage1: 1,
				rows: [],
				list: [],
				plus: false,
				system: [], //选择系统
				menu: [], //上级菜单
				id: '',
				title: '',
				num: 0, //jxs会报一个类型错误，就设了一个数字初始值
				pageNum: '1',
				pageRows: '10',
				form: {
					name: '',
					ico: '',
					authKey: '',
					sortNo: '',
					parentId: '',
					sysId: '',
					url: '',
					target: ''
				},
				formLabelWidth: '70px',
				add: {
					text: '新增',
					type: 'icon-add',
					icon: 'el-icon-plus',
				},
				out: {
					text: '删除',
					type: 'icon-delete',
					icon: 'el-icon-delete',
				},
				edit: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'el-icon-edit',
				},
				//表格对应的字段
				tableName: [{
						value: 'name',
						name: "菜单名称"
					},
					{
						value: 'authKey',
						name: "菜单标识"
					},
					{
						value: 'parentName',
						name: '上级菜单'
					},
					{
						value: 'ico',
						name: '菜单样式'
					},
					{
						value: 'sortNo',
						name: '排序'
					},
					{
						value: 'url',
						name: 'URL'
					},
				]
			}
		},
		methods: {
			//表格Checkbox 选中
			handleSelectionChange(val) {
				this.list = val;
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//新增
			addClick() {
				this.menu = [];
				this.title = '新增菜单';
				this.plus = true;
				this.form.name = '';
				this.form.ico = '';
				this.form.authKey = '';
				this.form.sortNo = '';
				this.form.parentId = '';
				this.form.sysId = '';
				this.form.url = '';
				this.form.target = '';
				delete this.form.id;
				this.followed = true;
			},
			//编辑
			editClick() {
				if(this.list.length == 0) {
					this.$message({
						message: '至少选择一条数据',
						type: 'warning'
					});
				} else if(this.list.length > 1) {
					this.$message({
						message: '只能选择一条数据',
						type: 'warning'
					});
				} else {

					this.title = '编辑菜单';
					this.plus = true;
					this.form.name = this.list[0].name;
					this.form.ico = this.list[0].ico;
					this.form.authKey = this.list[0].authKey;
					this.form.sortNo = this.list[0].sortNo;
					this.form.parentId = this.list[0].parentId;
					this.form.sysId = this.list[0].sysId;
					this.form.url = this.list[0].url;
					this.form.target = this.list[0].target;
					this.form.id = this.list[0].id;
					var p = {
						type: 0,
						sysId: this.form.sysId
					}
					this.$api.post('auth/queryAuthEntityBySysId', p, r => {
						this.menu = r.repData
					}, e => {
						console.log(e);
					})
				}

			},
			//删除
			deleteClick() {
				if(this.list.length == 0) {
					this.$message({
						message: '至少选择一条数据',
						type: 'warning'
					});
				} else {
					this.$confirm('此操作将永久删除该系统, 是否继续?', '提示', {
						confirmButtonText: '确定',
						cancelButtonText: '取消',
						type: 'warning'
					}).then(() => {
						this.ids = '';
						for(var i = 0; i < this.list.length; i++) {
							this.ids += this.list[i].id + ','
						}
						var p = {
							ids: this.ids.substring(0, this.ids.length - 1)
						}
						this.$api.post('auth/delete', p, r => {
							this.$message({
								message: '删除成功',
								type: 'success'
							});
							this.loadData();
						}, e => {
							console.log(e);
						})
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
				}
			},
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					type: "0",
					pageNum: this.pageNum,
					pageRows: this.pageRows
				}
				self.$api.post('auth/findAll', p, r => {
					self.rows = r.repData.rows
					self.num = r.repData.total
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
				self.$api.post('role/queryallsys', {}, r => {
					self.system = r.repData
				}, e => {
					this.$message({
						message: e.repData.repMsg,
						type: 'error'
					});
				})
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//新增确认
			confirm() {
				if(this.form.sysId == '') {
					this.$message({
						message: '请选择系统',
						type: 'warning'
					});
					return;
				}
				if(this.form.name == '') {
					this.$message({
						message: '请输入系统名称',
						type: 'warning'
					});
					return;
				}
				//				if(this.form.ico == '') {
				//					this.$message({
				//						message: '请输入菜单样式',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.authKey == '') {
				//					this.$message({
				//						message: '请输入菜单标识',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				if(this.form.sortNo == '') {
					this.$message({
						message: '请输入顺序',
						type: 'warning'
					});
					return;
				}
				//				if(this.form.menuId == ''){
				//					this.$message({
				//						message: '请选择上级菜单',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.url == '') {
				//					this.$message({
				//						message: '请输入URL',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.target == '') {
				//					this.$message({
				//						message: '请输入路劲',
				//						type: 'warning'
				//					});
				//					return;
				//				}

				var str = '';
				var url = '';
				if(this.title == '新增菜单') {
					str = '新增成功';
					url = 'auth/add';
				} else if(this.title == '编辑菜单') {
					str = '修改成功';
					url = 'auth/update';
				}
				this.$api.post(url, this.form, r => {
					this.$message({
						message: str,
						type: 'success'
					});
					this.plus = false;
					this.loadData();
				}, e => {
					console.log(e);
				})
			},
			//新增选择系统触发事件
			chose() {
				this.id = this.form.systemId
				this.form.parentId = '';
				var p = {
					type: 0,
					sysId: this.form.sysId
				}
				this.$api.post('auth/queryAuthEntityBySysId', p, r => {
					this.menu = r.repData
				}, e => {
					console.log(e);
				})
			}
		},
		created() {
			this.loadData();
		},
	}
</script>

