						})
					}).catch(() => {
						this.$message({
							type: 'info',
							message: '已取消删除'
						});
					});
				}
			},
			//初始化数据
			loadData() {
				var self = this;
				var p = {
					type: "0",
					pageNum: this.pageNum,
					pageRows: this.pageRows
				}
				self.$api.post('auth/findAll', p, r => {
					self.rows = r.repData.rows
					self.num = r.repData.total
				}, e => {
					console.log(e);
				})
				self.$api.post('role/queryallsys', {}, r => {
					self.system = r.repData
				}, e => {
					console.log(e);
				})
			},
			//分页
			handleSizeChange(val) {
				this.pageRows = val;
				this.loadData();
			},
			handleCurrentChange(val) {
				this.pageNum = val;
				this.loadData();
			},
			//新增确认
			confirm() {
				if(this.form.sysId == '') {
					this.$message({
						message: '请选择系统',
						type: 'warning'
					});
					return;
				}
				if(this.form.name == '') {
					this.$message({
						message: '请输入系统名称',
						type: 'warning'
					});
					return;
				}
				//				if(this.form.ico == '') {
				//					this.$message({
				//						message: '请输入菜单样式',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.authKey == '') {
				//					this.$message({
				//						message: '请输入菜单标识',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				if(this.form.sortNo == '') {
					this.$message({
						message: '请输入顺序',
						type: 'warning'
					});
					return;
				}
				//				if(this.form.menuId == ''){
				//					this.$message({
				//						message: '请选择上级菜单',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.url == '') {
				//					this.$message({
				//						message: '请输入URL',
				//						type: 'warning'
				//					});
				//					return;
				//				}
				//				if(this.form.target == '') {
				//					this.$message({
				//						message: '请输入路劲',
				//						type: 'warning'
				//					});
				//					return;
				//				}

				var str = '';
				var url = '';
				if(this.title == '新增菜单') {
					str = '新增成功';
					url = 'auth/add';
				} else if(this.title == '编辑菜单') {
					str = '修改成功';
					url = 'auth/update';
				}
				this.$api.post(url, this.form, r => {
					this.$message({
						message: str,
						type: 'success'
					});
					this.plus = false;
					this.loadData();
				}, e => {
					console.log(e);
				})
			},
			//新增选择系统触发事件
			chose() {
				this.id = this.form.systemId
				this.form.parentId = '';
				var p = {
					type: 0,
					sysId: this.form.sysId
				}
				this.$api.post('auth/queryAuthEntityBySysId', p, r => {
					this.menu = r.repData
				}, e => {
					console.log(e);
				})
			}
		},
		created() {
			this.loadData();
		},
	}
</script>

<style scoped>
	.el-form-item {
		margin-bottom: 22px;
		width: 49%;
		float: left;
	}
	
	. {
		width: 100%;
	}
	
	.dialog-footer {
		text-align: center;
	}
	
	.el-form .el-form-item:nth-child(3) {
		clear: both;
	}
	
	.maun {
		float: right;
	}
	/*整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.headDiv {
		/*padding: 20px;*/
		background: #fff;
		overflow: hidden;
		position: relative;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	
	.left {
		margin-left: 10px;
	}
</style>


